// blog aside

.join {
    background: $lightgray;
}

// media queries

@media screen and (min-width: 1199.98px) {
    .join {
        &_media {
            img {
                position: absolute;
            }

            &-left {
                width: 500px;
                left: -25%;
                top: 80px;
            }

            .group {
                width: 476px;
                height: 494px;
                position: absolute;
                right: -29%;
                top: 139px;

                .shape {
                    width: 476px;
                    height: 494px;
                }

                .speaker {
                    width: 350px;
                    height: 366px;
                    top: 0;
                    left: -114px;
                }
            }
        }
    }
}

@media screen and (min-width: 1399.98px) {
    .join {
        &_media {
            &-left {
                width: 550px;
                left: -15%;
                top: 44px;
            }

            .group {
                right: -18%;
                top: 33px;
            }
        }
    }
}

@media screen and (min-width: 1599.98px) {
    .join {
        &_media {
            &-left {
                width: auto;
                height: 555px;
                left: -3%;
                top: 23px;
            }

            .group {
                right: -11%;
                top: 54px;
            }
        }
    }
}

@media screen and (min-width: 1999.98px) {
    .join {
        &_media {
            .group {
                right: -1%;
            }
        }
    }
}
